import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { idLocale } from 'ngx-bootstrap';
import { Employee } from './model/employee';
import { environment } from 'src/environments/environment';
@Injectable({
  providedIn: 'root'
})
export class HttploginService {

  //baseurl:string="http://localhost:2121/";
  baseurl:string=environment.baseurl;

  //endpoint:string="/empId"
  constructor(private httplogin:HttpClient) { }

 login(obj:any)
 {
   return (this.httplogin.post(`${this.baseurl}doLogin/login`,obj))
 }
   
 getEmployee()
 {
     return (this.httplogin.get(`${this.baseurl}getEmployees`))
 }

 getEmployeeById(id)
 {
  return (this.httplogin.get(`${this.baseurl}getEmployee/${id}`))
 }

 getCountry()
 {
  return (this.httplogin.get(`${this.baseurl}getCountries`))
 }

 addEmployee(obj)
 {
    return (this.httplogin.post(`${this.baseurl}addEmployee`,obj));
 }
 UpdateEmployee(obj)
 {
   return (this.httplogin.put(`${this.baseurl}updateEmployee`,obj,{responseType:'text'}))
 }
 DeleteRecord(id)
 {
  return (this.httplogin.delete(`${this.baseurl}deleteEmployee/${id}`,{responseType:'text'}));
 }
}
